<template>
  <div class="container">
    <div>
      <v-tooltip right>
        <template v-slot:activator="{ on, attrs }">
          <h2 v-if="weatherData.temp != weatherData.temp_min" v-bind="attrs" v-on="on">{{ weatherData.temp }}&deg;F</h2>
        </template>
        <span>
      <dt>High</dt>
      <dd>{{ weatherData.temp_max }}&deg;F</dd>
      <dt>Low</dt>
      <dd>{{ weatherData.temp_min }}&deg;F</dd>
      </span>
      </v-tooltip>
      <dt>Humidity</dt>
      <dd>{{ weatherData.humidity }}%</dd>
    </div>
  </div>
</template>

<script>
export default {
  name: "WeatherData",
  data() {
    return {};
  },
  props: {
    weatherData: Object,
  },
};



</script>

<style scoped>
dl {
  padding: 5px;
  background: #e8e8e8;
}
dt {
  float: left;
  clear: left;
  width: 120px;
  text-align: right;
  font-weight: bold;
  color: blue;
}
dd {
  margin: 0 0 0 130px;
  padding: 0 0 0.5em 0;
}
dt::after {
  content: ":";
}
</style>